/* Palette generated by Material Palette - materialpalette.com/red/teal */
@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  src: local("Roboto"), local("Roboto-Regular"), url("https://fonts.gstatic.com/s/roboto/v15/2UX7WLTfW3W8TclTUvlFyQ.woff") format("woff"); }

@font-face {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 700;
  src: local("Roboto Bold"), local("Roboto-Bold"), url("https://fonts.gstatic.com/s/roboto/v15/d-6IYplOFocCacKzxwXSOD8E0i7KZn-EPnyo3HZu7kw.woff") format("woff"); }

html {
  background: #fafafa; }

html, body {
  margin: 0;
  padding: 0;
  font-family: roboto, sans-serif;
  -webkit-tap-highlight-color: transparent; }

pre,
code,
kbd,
samp,
tt {
  font-family: monospace;
  font-size: 1em; }

button:-moz-focus-inner {
  border: 0;
  padding: 0; }

button {
  cursor: pointer; }

.unbutton {
  display: inline-block;
  background: none;
  border: none;
  padding: 0;
  margin: 0;
  font: inherit;
  white-space: nowrap; }

.layout {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-flow: column;
  flex-flow: column;
  height: 100%; }

.main {
  -webkit-flex: 1;
  flex: 1;
  display: -webkit-flex;
  display: flex;
  overflow: hidden;
  position: relative; }

html,
body {
  height: 100%; }

.top-navbar-wrapper {
  z-index: 1;
  box-shadow: 0 0px 4px rgba(0, 0, 0, 0.4);
  width: 100vw;
  height: 3em; }
  .top-navbar-wrapper .top-navbar-container {
    height: 100%; }
    .top-navbar-wrapper .top-navbar-container .top-nav {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: row;
      flex-direction: row;
      list-style-type: none;
      margin: 0 0 0 5%;
      padding: 0 2% 0 0;
      -webkit-align-items: center;
      align-items: center;
      height: 100%;
      max-width: 85%;
      overflow-x: auto;
      overflow-y: hidden; }
      .top-navbar-wrapper .top-navbar-container .top-nav li {
        display: block;
        font-size: 4vw;
        white-space: nowrap;
        margin: 0;
        text-transform: uppercase; }
        .top-navbar-wrapper .top-navbar-container .top-nav li a {
          display: block;
          padding: 0.5em 0;
          text-decoration: none;
          padding: 0.5em 0;
          margin: 0 1em 0 0;
          color: #999999; }
        .top-navbar-wrapper .top-navbar-container .top-nav li a:active {
          color: #191919; }
        .top-navbar-wrapper .top-navbar-container .top-nav li a:hover {
          color: #333; }
        @media (min-width: 600px) {
          .top-navbar-wrapper .top-navbar-container .top-nav li {
            font-size: 1em; } }
    .top-navbar-wrapper .top-navbar-container .top-navbar-right {
      float: right; }

.masthead {
  display: block;
  width: 100vw;
  padding: 8px 0 0 0;
  text-align: center; }
  .masthead .navbar-toggle {
    border-color: #ddd; }
  .masthead .navbar-toggle .icon-bar {
    background-color: #888; }
  .masthead li {
    font-size: 1.4em; }
    @media (min-width: 600px) {
      .masthead li {
        font-size: 1.8em; } }
  .masthead .navbar-brand {
    font-size: 2em; }
    @media (min-width: 600px) {
      .masthead .navbar-brand {
        font-size: 2.4em; } }

#heroSlider {
  /* CUSTOMIZE THE CAROUSEL
-------------------------------------------------- */
  /* Carousel base class */
  /* Since positioning the image, we need to help out the caption */
  /* Declare heights because of positioning of img element */ }
  #heroSlider .carousel {
    height: 500px;
    margin-bottom: 60px; }
  #heroSlider .carousel-caption {
    z-index: 10; }
  #heroSlider .carousel .item {
    height: 500px;
    background-color: #777; }
  #heroSlider .carousel-inner > .item > img {
    position: absolute;
    top: 0;
    left: 0;
    min-width: 100%;
    height: 500px; }

#featured-list, #info-slider, #contact {
  padding: 27px 0 27px 0; }

.featured-list .tile-wrapper {
  padding: 10px; }
  .featured-list .tile-wrapper .tile {
    border: 1px solid #f0f0f0;
    height: 300px; }

.contact .left-pane h1 {
  font-size: 36px;
  text-transform: uppercase; }

.contact .right-pane h2 {
  text-transform: none; }
  .contact .right-pane h2 label {
    font-size: 24px;
    cursor: default;
    font-weight: normal; }

.contact .right-pane .contact-form .email-input-wrapper {
  width: 70%;
  float: left; }
  .contact .right-pane .contact-form .email-input-wrapper .email-input {
    width: 100%;
    padding: 15px 18px 15px 18px;
    background-color: #f8f8f8;
    border: 1px solid #f0f0f0; }
  @media (min-width: 600px) {
    .contact .right-pane .contact-form .email-input-wrapper {
      width: 60%; } }

.contact .right-pane .contact-form .contact-form-submit {
  float: left;
  width: 25%;
  max-width: 90px;
  text-align: center;
  text-transform: uppercase;
  border: none;
  line-height: 50px; }
  @media (min-width: 600px) {
    .contact .right-pane .contact-form .contact-form-submit {
      width: 90px; } }
